import React, { useState, useEffect } from "react";
import { ethers } from "ethers";
import contractABI from "../contracts/Undead.json";
import "./mint.css";
import {
  Button,
  Box,
  TextInput,
  ResponsiveContext,
  Grid,
  Card,
  Stack,
  CardBody,
  Image,
  CardHeader,
  Heading,
  Text,
} from "grommet";
const contractAddress = "0x23DFAe8beA1F7270e846d216e975DBDD72B11c9B";
import { Undead } from "../../gen/types/Undead";
import Background from "../assets/background.svg";
import Moon from "../assets/moon.svg";
import Logo from "../assets/undeadtownlogo.svg";
import Hand from "../assets/takemyhand.svg";
import Luna from "../assets/tombstone.svg";
import Layout from "../layouts/main";
import BearKiller  from '../assets/bearkilla.svg'

const getWindowEth = () => window !== undefined && window.ethereum;

const getWhitelistProof = (address: string, type: string) => {
  return fetch(`/whitelist/${type}/${address}.json`)
    .then((e) => e.json())
    .then((e) => {
      return e.proof;
    });
};

const requestConnectWallet = async () => {
  
  const eth = getWindowEth();
  if (eth) {
    try {

      const provider = new ethers.providers.Web3Provider(eth);
      await provider.send("eth_requestAccounts", []);
      return await provider.getSigner();
    } catch (e) {
      console.log(e);
    }
  }
};

const mint = async (contract: any, walletAddress: any, quantity) => {
  contract?.mint(quantity, { from: walletAddress, value: 0 }).then((e) => {
    console.log(e);
  }).catch(e => {
    console.log(e)
  });
}

const marketingMint = async (
  contract: any,
  proof: any,
  quantity: number,
  price = 25000000,
  walletAddress: any
) => {
  console.log(proof);
  console.log(quantity);
  console.log(walletAddress);
  return contract.publicSaleMint(quantity, {
    from: walletAddress,
    value: price * quantity,
  });
};

const callContract = async (contract: any, method: any, param: any) => {
  return await contract[method](param);
};

// markup
const IndexPage = () => {
  const size = React.useContext(ResponsiveContext);
  const [contract, setContract] = useState<Undead | null>(null);
  const [walletAddress, setWalletAddress] = useState<string | null>(null);
  const [proof, setProof] = useState<string | null>(null);
  const [mintQuantity, setMintQuantity] = useState<number>(1);
  const [myNFT, setMyNFT] = useState<[]>([]);
  const [nftMeta, setNftMeta] = useState<[]>([]);
  const [message, setMessage] = useState<string>("");
  const getMyNFT = async (address: string) => {
    return await callContract(contract, "tokensOf", address);
  };

  const getMetaFile = async (tokenId: number) => {
    const url = await callContract(contract, "tokenURI", tokenId);
    return fetch(url).then((e) => e.json());
  };

  const initializeContract = async () => {
    const signer = await requestConnectWallet();
    debugger;
    if (signer) {
      signer.getAddress().then((address) => {
        setWalletAddress(address.toLowerCase());
      });

      const contractObject = new ethers.Contract(
        contractAddress,
        contractABI,
        signer
      ) as Undead;
      setContract(contractObject);
    }
  };


  useEffect(() => {
    Promise.all(
      myNFT.map((nftTokenId) => {
        return getMetaFile(nftTokenId);
      })
    ).then((allMeta: any) => {
      setNftMeta(allMeta);
    });
  }, [myNFT]);

  return (
    <Layout>
      <div className="container">
        <audio autoplay="true" controls hidden>
          <source src="/assets/background_music.wav" type="audio/ogg" />
          Your browser does not support the audio element.
        </audio>
        <div className="wallet" onClick={() => initializeContract()}>
                    {
                      walletAddress ? "Connected" : "Connect wallet"
                    }
        </div>
        <ResponsiveContext.Consumer>
          {(size) => {
            return size === "small" ? (
              <div className="flex-row">
                <div className="moon">
                  <Logo className="logo"></Logo>

                </div>

                <div className="hand-c">
                <Hand className="hand buttonSVG" onClick={() => mint(contract, walletAddress, 1)}></Hand>
                </div>
                <div className="luna-c">
                <Luna className="luna"></Luna>
                </div>
              </div>
            ) : (
              <>

                <div className="flex-row">
                <div className="moon">
                  <Logo className="logo"></Logo>

                </div>


                <div className="luna-c">

                <Hand className="hand buttonSVG" onClick={() => mint(contract, walletAddress, 1)}></Hand>

                </div>
                <div className="o-c">
                <Luna className="luna"></Luna>
                
                <BearKiller class="bearkilla"></BearKiller>
                </div>
              </div>
              </>
            );
          }}
        </ResponsiveContext.Consumer>
      </div>
    </Layout>
  );
};

export default IndexPage;
